.inner-intro {
  position: relative;
}

.inner-intro::after {
  content: " ";
  min-width: 320px;
  height: 1px;
  background-color: #e5e5e5;
  bottom: 0;
  left: 0;
  position: absolute;
}

.inner-intro__container::after {
  content: url("../img/intro-triangle.svg");
  position: absolute;
  height: 57px;
  top: 101px;
  left: 0;
}

.inner-intro__img {
  background-image: url("../img/catalog/back-desktop-catalog.jpg");
  background-repeat: no-repeat;
  background-position: center;
  height: 38px;
  width: 229px;
  background-color: @lightgrey;
  padding-left: 45px;
  padding-right: 45px;
  padding-top: 52px;
  padding-bottom: 68px;
  position: relative;
}

.catalog-main__text-container {
  padding-top: 26px;
  padding-left: 23px;
  padding-right: 23px;
  padding-bottom: 17px;
}

.catalog-main__title {
  font-family: "pt sans bold", "arial", sans-serif;
  line-height: 26px;
  font-size: 18px;
  text-align: center;
  text-transform: uppercase;
  margin: 0;
}

.catalog-main__text {
  text-align: center;
  text-transform: uppercase;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  padding-top: 15px;
}
