@font-face {
  font-family: "pt sans bold";
  font-weight: normal;
  font-style: bold;
  font-display: swap;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
@font-face {
  font-family: "pt sans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
img {
  width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
html {
  height: 100%;
  font-weight: 400;
  font-family: "pt sans", "arial", sans-serif;
  line-height: 21px;
  font-size: 14px;
}
.page-body {
  min-height: 100%;
  margin: 0;
  color: #000000;
  opacity: 0.5;
}
@media (min-width: 1200px) {
  .page-body {
    background-color: #e7e5e5;
  }
}
.page-body__container {
  width: 100%;
  margin: 0 auto;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-body__container {
    width: 100%;
  }
}
@media (min-width: 1200px) {
  .page-body__container {
    margin: 0 auto;
    max-width: 1200px;
    -webkit-box-shadow: 0px 0px 14px 2px rgba(34, 60, 80, 0.24);
    -moz-box-shadow: 0px 0px 14px 2px rgba(34, 60, 80, 0.24);
    box-shadow: 0px 0px 14px 2px rgba(34, 60, 80, 0.24);
  }
}
.page-main {
  flex-grow: 1;
  width: 100%;
}
.page-header {
  position: relative;
  height: 56px;
  z-index: 50;
}
@media (min-width: 768px) {
  .page-header {
    display: flex;
    justify-content: space-between;
  }
}
.page-header__link {
  display: flex;
  justify-content: center;
}
.page-header__link:active {
  opacity: 0.8;
}
.page-header__link:hover {
  opacity: 0.6;
}
.page-header__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .page-header__link {
    position: absolute;
    transform: translateX(-50%);
    left: 50%;
    z-index: 10;
  }
}
.page-header__logo {
  z-index: 2;
}
.page-header__logo:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .page-header__logo {
    width: 187px;
    height: 84px;
  }
}
@media (min-width: 1200px) {
  .page-header__logo {
    height: 97px;
  }
}
.main-nav {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 3;
}
@media (min-width: 768px) {
  .main-nav {
    position: relative;
    display: flex;
  }
}
.main-nav__container {
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__container {
    display: flex;
    justify-content: center;
  }
}
