    box-shadow: none;
    color: #000000;
  }
  .site-list__link:hover {
    background-color: transparent;
    color: #81b3d2;
  }
  .site-list__link:active {
    background-color: transparent;
    color: #000000;
    opacity: 0.3;
  }
  .site-list__link:focus {
    color: #669ec0;
    outline: none;
    border: none;
  }
}
