.main-nav__toggle {
  position: absolute;
  right: 0;
  top: 0;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--nojs {
  display: none;
}
.toggle-button {
  border: 0;
  background-color: transparent;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-left: 16px;
  padding-right: 19px;
  cursor: pointer;
}
.toggle-button__svg {
  fill: #81b3d2;
}
.toggle-button__svg:hover {
  opacity: 0.6;
}
.toggle-button__svg:active {
  opacity: 0.3;
}
.toggle-button__svg:focus {
  outline: none;
}
.main-nav__cross {
  position: absolute;
  right: 0;
  top: 0;
  z-index: 3;
}
.cross-button {
  border: 0;
  background-color: transparent;
  padding-top: 17px;
  padding-bottom: 17px;
  padding-left: 16px;
  padding-right: 16px;
  cursor: pointer;
}
.cross-closed {
  display: none;
}
.cross--opened {
  display: block;
  z-index: 100;
}
.cross-button__svg:hover {
  opacity: 0.6;
}
.cross-button__svg:active {
  opacity: 0.3;
}
